.message {
    display: flex;
    margin-bottom: 0.3rem;
    flex-direction: column;    
    
}
.Mess_seen_text {
    --opacityhover: hidden;
  }
.messageTop ,.messageBottom
{
    display: flex;
    margin: 0px 1rem 0px 1rem;
    align-items: center;
} .firstMessage,.midMessage
{

    margin-left: 3.7rem;
    margin-bottom: 0.2rem;
}
.firstMessage .messageText{
    border-radius: 2rem 2rem 2rem .3rem ;

}
.endMessage .messageText{
    border-radius: 0.3rem 2rem 2rem 2.5rem ;

}
.own .firstMessage, .own .midMessage
{

    margin-right: 3.7rem;
    margin-bottom: 0.2rem;
}
.firstMessage .messageText{
    border-radius: 2rem 2rem 2rem .3rem ;

}
.endMessage .messageText{
    border-radius: 0.3rem 2rem 2rem 2.5rem ;

}
.avatar_dot
{
    display: flex;
    margin: 0 0.9rem 0 0;
    margin-top: 0px;
    margin-right: 0.9rem;
    margin-bottom: 0px;
    margin-left: 0px;
    align-items: flex-end;
    justify-content: flex-end;
}
.own .messageTop, .own .Seen_field,.own .Mess_seen_text

{
    flex-direction: row-reverse;

}
.Mess_seen_container
{
    display: flex;
    flex-direction: column;
}
.Seen_field
{
    --hiden_seen:hidden;
    display: flex;
    align-items: center;

}

.Seen_field:hover
{
    --hiden_seen:visible
}
.Seen_field p
{
    visibility: var(--hiden_seen);
    transition: 0.1s linear;
    padding-right: 0.4rem;
}
.message.own
{
    display: flex;
    flex-direction: column;
}
.messageImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.messageText {
    padding: .5rem 1rem .5rem 1rem;
    font-size: medium;
    border-radius:.5rem 1rem 1rem .5rem;
    background-color: #a8aaad9b;
    color: rgb(0, 0, 0);
    display: inline-block;
    max-width: 100%;
    overflow-wrap: break-word;
    margin: 0px;
}

.Mess_seen_text,.forget_pass
{
    display: flex;
    position: relative;
    align-items: center;
    width: 50%;
}
.messageBottom {
   
    position: relative;
}
.messageBottom p{
    border-radius: 10px;
    color: white !important;
    font-size: 0.7rem;
    padding: 0.4rem;
    transition: 0.1s linear;
    visibility: var(--opacityhover);
    background-color: rgba(141, 141, 141, 0.712);
}

.Mess_seen_text:hover{
    --opacityhover:visible;
    color: black;
    
}
.message.own .messageText {
    background-color: rgb(2, 0, 0);
    border-radius: 1rem .5rem .5rem 1rem;
    color: rgb(255, 255, 255);
}
.center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.newMessage svg{
    width: 20px;
    height: 20px;
}
.newMessage{
    width: 50px;
    position: fixed;
    bottom: 20px;
    right: 9px;
    aspect-ratio: 1;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.175);
    transition: 250ms linear;
}
.newMessage:hover::before {
    content: "Tin nhắn mới";
    position: absolute;
    right: 110%;
    font-size: .8rem;
    background-color: white;
    display: flex;
    border-radius: .5rem;
    align-items: center;
    justify-content: center;
    width: 90px;
    height: 30px;
}